<template>
  <b-row>
    <b-col cols="12">
      <h2>
        Edition client
        <b-link href="#/">(Liste clients)</b-link>
      </h2>
      <b-jumbotron>
        <template slot="lead">
          Numéro client: {{client.numClient}}<br>
          Nom: {{client.nom}}<br>
          Prénom: {{client.prenom}}<br>
          H/F: {{client.genre}}<br>
          adresse: {{client.adresse}}<br>
          Date de naissance: {{client.dateNaissance}}<br>
        </template>
        <hr class="my-4">
        <b-btn variant="success" @click.stop="editclient(client._id)">Edit</b-btn>
        <b-btn variant="danger" @click.stop="deleteclient(client._id)">Delete</b-btn>
      </b-jumbotron>
    </b-col>
  </b-row>
</template>

<script>

import axios from 'axios'

export default {
  name: 'ShowClient',
  data () {
    return {
      client: []
    }
  },
  created () {
    axios.get(`http://localhost:3000/client/` + this.$route.params.id)
      .then(response => {
        this.client = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    editclient (clientid) {
      this.$router.push({
        name: 'EditClient',
        params: { id: clientid }
      })
    },
    deleteclient (clientid) {
      axios.delete('http://localhost:3000/client/' + clientid)
        .then((result) => {
          this.$router.push({
            name: 'ClientList'
          })
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>

<style>
  .jumbotron {
    padding: 2rem;
  }
</style>



