<template>
  <div>
    <Menu/>
    <md-dialog
        :md-active.sync="active">
        <md-dialog-title>Comptes client</md-dialog-title>
    <md-field>
      <label>Nom</label>
      <md-input v-model="client.nom"
      readonly
        type="string"></md-input>
    </md-field>
    <md-field>
      <label>Prenom</label>
      <md-input v-model="client.prenom"
      readonly
       type="string"
       ></md-input>
    </md-field>
    <el-table
      :data="cptClient"
      height="300"
      style="width: 100%"
      border>
      <el-table-column
        label="Compte"
        width="250"
        prop='typecpt'
        sortable>
      </el-table-column>
      <el-table-column
        label="N° de compte"
        width="300"
        prop='numcpt'
        >
      </el-table-column>
        <el-table-column
          label="Solde"
          width="150"
          cell-style="green"
          prop='solde'
          >
      </el-table-column>
        <el-table-column
        label="Opérations"
        width="120">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">
            Editer
          </el-button>
          </template>
        </el-table-column>
      </el-table>
    <div>
  <el-checkbox-group v-model="clients">
    <el-checkbox-button v-for="item in clients" :label="compte" :key="item">{{item}}</el-checkbox-button>
  </el-checkbox-group>
</div>
    <div class="align">
      <md-button class="md-raised md-primary" @click="onSubmit">Ok</md-button>
      <md-button class="md-raised md-accent" @click="onCancel">Annulé</md-button>
    </div>
  </md-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import Menu from './Menu'
export default {
  data: () => ({
    active: true,
    client: [],
    detail: [],
    clients: {},
    cptClient: [],
    userId: '',
    numcpt: ''
  }),
  created () {
    axios.get(`http://localhost:3000/client/` + this.$route.params.id)
      .then(response => {
        this.client = response.data
        this.userId = response.data._id
        axios.get(`http://localhost:3000/cptclient/`, {
          params: {
            userId: this.userId
          }
        }).then(response => {
          this.cptClient = response.data
        }).catch(e => {
          this.errors.push(e)
        })
      }).catch(e => {
        this.errors.push(e)
      })
  },
  components: {
    Menu
  },
  methods: {
    handleEdit (index, row) {
      this.$router.push({
        name: 'DetailsCpt',
        params: { id: this.userId, numcpt: row.numcpt, idcpt: row._id }
      })
    },
    onSubmit (evt) {
      evt.preventDefault()
      axios.put(`http://localhost:3000/client/` + this.$route.params.id, this.client)
        .then(response => {
          this.$router.push({
            name: 'ClientList'
          })
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    onCancel: function () {
      this.active = false
      this.$router.push({
        name: 'ClientList'
      })
    }
  }
}
</script>
<style media="screen">
.md-list {
   width: 320px;
   max-width: 100%;
   display: inline-block;
   vertical-align: top;
   border: 1px solid rgba(#000, .12);
 }
.align{
  text-align: right;
}
  .md-dialog-container{
    margin-top: 400px;
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    margin-left: 50%;
    min-width: 800px;
    height: 600px;
  }
</style>



