<template>
  <div>
    <md-dialog
        :md-active.sync="active">
        <md-dialog-title>Propriétés utilisateur</md-dialog-title>
    <md-field>
      <label>Nom</label>
      <md-input v-model="form.name"
      maxlength="50"
        type="string"></md-input>
    </md-field>
    <md-field>
      <label>e-Mail</label>
      <md-input v-model="form.email"
       type="string"
         maxlength="30"
       ></md-input>
    </md-field>
    <md-field>
      <label>Password</label>
      <md-input v-model="form.password" type="password"></md-input>
    </md-field>
    <md-field>
      <label>confirm password</label>
      <md-input v-model="passwordConf" type="password"></md-input>
    </md-field>
    <md-field>
        <md-switch v-model="form.is_admin" class="md-primary">Administrateur</md-switch>
       </md-field>
    <div class="align">
      <md-button class="md-raised md-primary" @click="onSubmit">Ok</md-button>
      <md-button class="md-raised md-accent" @click="onCancel">Annulé</md-button>
    </div>
  </md-dialog>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      is_admin: localStorage.getItem('is_admin') || '',
      passwordConf: '',
      active: true,
      form: {
        name: '',
        email: '',
        password: '',
        is_admin: false,
        count: 0
      }
    }
  },
  methods: {
    onSubmit (evt) {
      if (this.passwordConf !== this.form.password) { alert('Vérifier la confirmation des mots de passe') } else {
        this.dialogFormVisible = false
        evt.preventDefault()
        axios.post(`http://localhost:3000/user/register`, this.form)
          .then(response => {
            this.form.is_admin = ''
            this.form.email = ''
            this.form.password = ''
            this.$router.push({
              name: 'UserList',
              params: { id: response.data._id }
            })
          })
          .catch(e => {
            this.errors.push(e)
          })
      }
    },
    onCancel () {
      this.active = false
      this.$router.push({
        name: 'UserList'
      })
    }
  }
}
</script>
<style media="screen">
.md-button{
  width: 50px;
}
.md-dialog{
  margin-top: 100px;
}
  .md-dialog-container{
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    margin-left: 50%;
    min-width: 600px;
    height: 550px;
  }
</style>



