import Vue from 'vue'
import Router from 'vue-router'
import Accueil from '@/components/Accueil.vue'
import ClientList from '@/components/ClientList'
import ShowClient from '@/components/ShowClient'
import CreateClient from '@/components/CreateClient'
import EditClient from '@/components/EditClient'
import store from '@/store.js'
import About from '@/components/About.vue'
import Register from '@/components/Register.vue'
import StartLogin from '@/components/StartLogin.vue'
import UserList from '@/components/UserList.vue'
import EditUsers from '@/components/EditUsers.vue'
import Menu from '@/components/Menu.vue'
import Compte from '@/components/Compte.vue'
import Depenses from '@/components/Depenses.vue'
import ModalComptes from '@/components/ModalComptes.vue'
import DetailsCpt from '@/components/DetailCpt.vue'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/accueil',
      name: 'Accueil',
      component: Accueil
    },
    {
      path: '/startLogin',
      name: 'startLogin',
      component: StartLogin
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/clientList',
      name: 'ClientList',
      component: ClientList
    },
    {
      path: '/show-client/:id',
      name: 'ShowClient',
      component: ShowClient
    },
    {
      path: '/list_users',
      name: 'UserList',
      component: UserList
    },
    {
      path: '/add-client',
      name: 'CreateClient',
      component: CreateClient
    },
    {
      path: '/edit-client/:id',
      name: 'EditClient',
      component: EditClient
    },
    {
      path: '/edit-users/:id',
      name: 'EditUsers',
      component: EditUsers
    },
    {
      path: '/menu',
      name: 'Menu',
      component: Menu
    },
    {
      path: '/compte/:id',
      name: 'Compte',
      component: Compte
    },
    {
      path: '/depense/:id',
      name: 'Depenses',
      component: Depenses
    },
    {
      path: '/tscomptes/:id',
      name: 'ModalComptes',
      component: ModalComptes
    },
    {
      path: '/detailscpt/:id',
      name: 'DetailsCpt',
      component: DetailsCpt
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters.isLoggedIn) {
      next()
      return
    }
    next('/')
  } else {
    next()
  }
})

export default router



