<template>
<div>
    <el-menu
  class="el-menu-demo"
  mode="horizontal"
  background-color="#c3d3d0"
  text-color="#d74314"
  active-text-color="#246b08">
  <el-submenu index="1">
    <template slot="title">Accueil</template>
    <el-menu-item index="1-1"><b-link href="/accueil">Accueil</b-link></el-menu-item>
  <el-menu-item index="1-2"><b-link href="/"><a @click="logout">Deconnexion</a></b-link></el-menu-item>
</el-submenu>
  <el-submenu v-if="is_admin === 'true'" index='2'>
    <template slot="title">gestion des users</template>
  <el-menu-item index="2-1"><b-link href="/list_users">liste des utilisateurs</b-link></el-menu-item>
  <el-menu-item index="2-2"><b-link href="/register">Nouvel utilisateur</b-link></el-menu-item>
</el-submenu>
  <el-submenu index="3">
    <template slot="title">Clients</template>
    <el-menu-item index="3-1"><b-link href="/clientList">Liste clients</b-link></el-menu-item>
  </el-submenu>
<el-menu-item index="4"><b-link href="/about"><a>?</a></b-link></el-menu-item>
</el-menu>
</div>
</template>
<script>
export default {
  name: 'Menu',
  data () {
    return {
      is_admin: localStorage.getItem('is_admin') || ''
    }
  },
  methods: {
    logout: function () {
      localStorage.removeItem('token')
      this.$router.push({
        name: '/'})
    }
  }
}
</script>



