<template>
  <div>
        <el-form :model="client">
          <el-form-item label="Nom" :label-width="formLabelWidth">
            <el-input v-model="client.nom" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="Prénom" :label-width="formLabelWidth">
            <el-input v-model="client.prenom" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="Adresse" :label-width="formLabelWidth">
            <el-input v-model="client.adresse" type="textarea" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="Numéro" :label-width="formLabelWidth">
            <el-input v-model="client.telephone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="e-Mail" :label-width="formLabelWidth">
            <el-input v-model="client.email"></el-input>
          </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="danger" @click="cancel">Annuler</el-button>
          <el-button type="success" @click="onSubmit">Confirmer</el-button>
          <el-button type="success" @click="onAdd" >Ajouter des comptesr</el-button>
        </span>
</div>
  </template>
<script>
import axios from 'axios'
export default {
  name: 'Editclient',
  data () {
    return {
      formLabelWidth: '120px',
      dialogFormVisible: true,
      dialogCompteVisible: true,
      client: {}
    }
  },
  created: function () {
    axios.get(`http://localhost:3000/client/` + this.$route.params.id)
      .then(response => {
        this.client = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    cancel: function () {
      this.$router.push({
        name: 'ClientList'
      })
    },
    onAdd () {
      this.$router.push({
        name: 'ModalComptes',
        params: { id: this.client._id }
      })
    },
    onSubmit (evt) {
      evt.preventDefault()
      axios.put(`http://localhost:3000/client/` + this.$route.params.id, this.client)
        .then(response => {
          this.$router.push({
            name: 'ClientList'
          })
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>
<style media="screen">
  .el-dialog{
    border-radius: 10px;
  }
</style>



