<template>
  <div class="diagGlobal">
    <md-dialog :md-active.sync="active">
    <md-card-header>
  <div class="md-title">Identité du client</div>
</md-card-header>
    <form novalidate class="md-layout" @submit.prevent="validateUser">
  <md-card-content>
    <md-field :class="getValidationClass('nom')">
      <label for="nom">Nom</label>
        <md-input name="nom" id="nom" v-model="client.nom" :disabled="sending" />
        <span class="md-error" v-if="!$v.client.nom.required">Le nom est requis</span>
    </md-field>
    <md-field :class="getValidationClass('prenom')">
      <label for="prenom">Prenom</label>
      <md-input name="prenom" id="prenom" v-model="client.prenom" :disabled="sending" />
      <span class="md-error" v-if="!$v.client.prenom.required">Le nom est requis</span>
      </md-field>
    <md-field :class="getValidationClass('adresse')">
      <label>Adresse</label>
      <md-textarea name="adresse" id="adresse" v-model="client.adresse" :disabled="sending"></md-textarea>
      <span class="md-error" v-if="!$v.client.adresse.required">L'adresse est requis</span>
    </md-field>
  <md-field :class="getValidationClass('telephone')">
    <label for="telephone">Numéro de téléphone</label>
      <md-input name="telephone" id="telephone" v-model="client.telephone" :disabled="sending" />
      <span class="md-error" v-if="!$v.client.telephone.required">Le numero est requis</span>
   </md-field>
        <md-field :class="getValidationClass('email')">
         <label for="email">Email</label>
         <md-input type="email" name="email" id="email" autocomplete="email" v-model="client.email" :disabled="sending" />
         <span class="md-error" v-if="!$v.client.email.required">The email is required</span>
         </md-field>
    <div class="align">
       <md-button type="submit" class="md-raised md-primary" :disabled="sending">Ok</md-button>
      <md-button class="md-raised md-accent" @click="onCancel">Annulé</md-button>
    </div>
  </md-card-content>
</form>
</md-dialog>
  </div>
</template>
<script>
import axios from 'axios'
import { validationMixin } from 'vuelidate'
import {
  required,
  email
} from 'vuelidate/lib/validators'

export default {
  name: 'CreateClient',
  data: () => ({
    mixins: [validationMixin],
    userSaved: false,
    sending: false,
    lastUser: null,
    active: true,
    client: {
      nom: null,
      prenom: null,
      adresse: null,
      telephone: null,
      email: null
    },
    value: '',
    options: []
  }),
  validations: {
    client: {
      nom: {
        required
      },
      prenom: {
        required
      },
      adresse: {
        required
      },
      telephone: {
        required
      },
      email: {
        required,
        email
      }
    }
  },
  created: function () {
    axios.get(`http://localhost:3000/typecpt/`)
      .then(response => {
        this.options = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.client[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.client.nom = null
      this.client.prenom = null
      this.client.adresse = null
      this.client.telephone = null
      this.client.email = null
    },
    onCancel () {
      this.active = false
      this.$router.push({
        name: 'ClientList'
      })
    },
    saveUser () {
      this.sending = true
      this.client.nom = this.client.nom.toUpperCase()
      this.dialogFormVisible = false
      window.setTimeout(() => {
        this.lastUser = `${this.client.nom} ${this.client.prenom}`
        this.userSaved = true
        this.sending = false
        this.clearForm()
      }, 1500)
    },
    validateUser () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.saveUser()
        axios.post(`http://localhost:3000/client`, this.client)
          .then(response => {
            this.$router.push({
              name: 'ClientList'
            })
          })
          .catch(e => {
            this.errors.push(e)
          })
      }
    }
  }
}
</script>
<style media="screen">
.md-progress-bar {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
.align{
  text-align: right;
}
.md-button{
  border-radius: 10px;
  width: 50px;
}
.md-layout {
  transition: .3s $md-transition-stand-timing;
}
  .md-dialog-container{
    border-radius: 10px;
    margin-top: 350px;
    height: 600px;
    padding-left: 10px;
    padding-right: 10px;
    margin-left: 50%;
    min-width: 600px;
  }
  .md-input{
    min-width: 500px;
  }
</style>



