import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

var state = {
  typeCpt: []
}
const getters = {
  typeCpt: state => state.typeCpt
}
const mutations = {
  GET_CPT: (state, {typeCpt}) => {
    state.typeCpt = typeCpt
    console.log('store', typeCpt)
  }
}
const actions = {
  LOAD_CPT: ({commit}) => {
    axios.get(`http://localhost:3000/typecpt/`).then(response => {
      commit('GET_CPT', {typeCpt: response.data})
      // console.log(response.data)
    })
  }
}
export default new Vuex.Store({
  state: state,
  mutations: mutations,
  getters: getters,
  actions: actions
})



