<template>
  <div id="userList">
    <Menu/>
  <h2>
  Liste de utilisateurs
  <b-link href="/"></b-link>
</h2>
<br>
  <br>
  <div class="buttonNouveauUser">
    <el-button type="primary" @click="onNewClient">Nouvel utilisateur</el-button>
  </div>
  <br>
  <br>
  <el-table
    :data="user"
    style="width: 80%">
      <el-table-column
      label="Nom"
      width="180"
      prop='name'
      sortable>
    </el-table-column>
    <el-table-column
      label="email"
      prop='email'
      width="180"
      sortable>
    </el-table-column>
    <el-table-column
      label="Opérations">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">Editer</el-button>
        <el-button
          size="mini"
          type="danger"
          icon="el-icon-delete"
          @click="deleteUser(scope.$index, scope.row)" circle></el-button>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
import axios from 'axios'
import Menu from './Menu'
export default {
  name: 'UserList',
  data () {
    return {
      is_admin: localStorage.getItem('is_admin') || '',
      user: []
    }
  },
  components: {
    Menu
  },
  created () {
    axios.get(`http://localhost:3000/user/`)
      .then(response => {
        this.user = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    handleEdit (index, row) {
      this.$router.push({
        name: 'EditUsers',
        params: { id: row._id }
      })
    },
    deleteUser (index, row, rows) {
      var myThis = this
      myThis.$swal.fire({
        title: 'Etes vous sûr ?',
        text: 'Vous ne pourrez plus accéder',
        icon: 'warning',
        showCancelButton: true,
        showLoaderOnConfirm: true,
        confirmButtonText: 'Oui, supprimer !',
        cancelButtonText: 'Non, annuler !',
        cancelButtonColor: '#d33',
        confirmButtonColor: '#3085d6',
        preConfirm: function () {
          return new Promise(function (resolve, reject) {
            axios.delete('http://localhost:3000/user/' + row._id)
            setTimeout(function () {
              resolve()
            }, 4000)
            myThis.user.splice(index, 1)
            myThis.$swal.fire(
              'Suppression effectuée !',
              'L\'utilisateur a été supprimé définitivement.',
              'success')
          })
        }
      })
    },
    onNewClient () {
      this.$router.push('register')
    }
  }
}
</script>
<style>
  .buttonNouveauUser{
    margin-left: 20px;
  }
</style>



