<template lang="html">
  <div>
    <span>Compte N°: {{this.numcpt}}</span>
    <br>
    <br>
    <div>
    <el-button type="success" @click="onAdd" >Ajouter une opération</el-button>
    <el-button type="danger" @click="onCancel">Retour au comptes</el-button>
    <br>
    <br>
    <div class="align">
      <div class="dec" v-if="this.tot < 0">
        <span>Compte à decouvert</span>
      </div>
  <span class="demo-input-label">Solde du compte</span>
    <el-input class="tot" v-model="this.tot"></el-input>
    </div>
  </div>
  <el-table
  :data="detailsC"
  :summary-method="getSummariesC"
  show-summary
  style="width: 100%"
  height="200">
  <el-table-column
    prop="nature"
    label="nature"
    width="250">
  </el-table-column>
  <el-table-column
    prop="credit"
    label="Crédit"
    width="180">
  </el-table-column>
</el-table>
<el-table
  :data="details"
  :summary-method="getSummariesD"
  show-summary
  style="width: 100%"
  height="200">
<el-table-column
  prop="nature"
  label="nature"
  width="250">
</el-table-column>
<el-table-column
v-show="details.debit!=null"
  prop="debit"
  label="Débit"
  width="180">
</el-table-column>
</el-table>
</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      client: {},
      numcpt: '',
      totC: '',
      totD: '',
      tot: '',
      details: [],
      detailsC: [],
      dialogTableVisible: true
    }
  },
  created () {
    this.numcpt = this.$route.params.numcpt
    this.idcpt = this.$route.params.idcpt
    console.log('idcptDetail', this.idcpt)
    axios.get(`http://localhost:3000/detailcpt/`, {
      params: {
        userId: this.$route.params.id,
        numcpt: this.$route.params.numcpt
      }
    }).then(response => {
      this.details = response.data
    })
      .catch(e => {
        this.errors.push(e)
      })
    axios.get(`http://localhost:3000/detailcptcredit/`, {
      params: {
        userId: this.$route.params.id,
        numcpt: this.$route.params.numcpt
      }
    }).then(response => {
      this.detailsC = response.data
    })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    onCancel: function () {
      this.client.solde = this.tot
      axios.put(`http://localhost:3000/cptclient/` + this.idcpt, this.client)
        .then(response => {
          console.log(response)
          this.dialogTableVisible = false
          this.$router.push({
            name: 'Compte'
          })
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    getSummariesC (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = 'Total crédit'
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (!values.every(value => isNaN(value))) {
          sums[1] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          this.totC = Number(sums[1])
          if (this.totD === null) { this.tot = this.totC }
          this.tot = this.totC - this.totD
        } else {
          sums[index] = 'N/A'
        }
      })
      return sums
    },
    getSummariesD (param) {
      const { columns, data } = param
      const sumD = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sumD[index] = 'Total débit'
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (!values.every(value => isNaN(value))) {
          sumD[1] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          this.totD = Number(sumD[1])
          if (this.totD === null) { this.tot = this.totC }
          if (this.totC === null) { this.tot = this.totD }
          this.tot = this.totC - this.totD
        } else {
          sumD[index] = 'N/A'
        }
      })
      return sumD
    },
    onAdd: function () {
      this.$router.push({
        name: 'Depenses',
        params: {
          idcpt: this.idcpt,
          id: this.$route.params.id,
          numcpt: this.$route.params.numcpt,
          totD: this.totD,
          totC: this.totC
        }
      })
    }
  }
}
</script>

<style lang="css" scoped>
.align{
  text-align: left;
  width: 200px;
}
.dec{
  height: 50px;
  width: 500px;
  color: red;
  font-size: 30px;
}
.tot{
  font-size: 20px;
  }
</style>



