<template>
  <div id="app" style="height: 800px;">
    <md-dialog
    :md-active.sync="active">
    <StartLogin v-if='!visibleToken'></startLogin>
        <router-view/>
        <BackGround/>
</md-dialog>
  </div>
</template>
<script>
import BackGround from './components/BackGround'
import StartLogin from './components/StartLogin'
export default {
  data () {
    return {
      active: true,
      visibleToken: localStorage.getItem('token')
    }
  },
  components: {
    BackGround,
    StartLogin
  },
  computed: {
    isLoggedIn: function () {
      return this.$store.getters.isLoggedIn
    }
  },
  methods: {
    logout: function () {
      this.$store.dispatch('logout')
        .then(() => {
          this.$router.push('/')
        })
    }
  },
  created: function (logout) {
    this.$http.interceptors.response.use(undefined, function (err) {
      return new Promise(function (resolve, reject) {
        if (err.status === 401 && err.config && !err.config.__isRetryRequest) {
          this.$store.dispatch(logout)
        }
        throw err
      })
    })
  }
}
</script>
<style media="screen">
.md-dialog-container{
  padding-top: 200px;
  width: auto;
  height: auto !important;
}
</style>



