<template lang="html">
  <div>
    <el-form  :inline="true" class="demo-form-inline">
      <el-form-item label="Type de comptes">
      <el-select v-model="value" placeholder="Type de compte">
        <el-option
        v-for="item in typeCpt"
        :key="item.type"
        :value="item.type">
      </el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="Solde">
    <el-input v-model="cptForm.solde"></el-input>
  </el-form-item>
  <el-form-item label="N° compte">
    <el-input v-model="cptForm.numcpt" width="150"></el-input>
  </el-form-item>
   <el-button type="success" @click="onAdd">Ajouter le compte</el-button>
  </el-form>
    <el-table
     :data="client"
     style="width: 100%">
     <el-table-column
       fixed
       prop="numcpt"
       label="N° de compte"
       width="400">
     </el-table-column>
     <el-table-column
       fixed
       prop="compte"
       label="Type de compte"
       width="400">
     </el-table-column>
     <el-table-column
       fixed
       prop="solde"
       label="Solde"
       width="100">
     </el-table-column>
   </el-table>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ModalComptes',
  data () {
    return {
      formLabelWidth: '120px',
      dialogFormVisible: true,
      client: [],
      cptForm: {
        solde: '',
        numcpt: ''
      },
      typeCpt: [],
      value: ''
    }
  },
  created () {
    console.log('id', this.$route.params.id)
    axios.get(`http://localhost:3000/typecpt`)
      .then(response => {
        this.typeCpt = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    onAdd () {
      this.cptForm.user_id = this.$route.params.id
      this.cptForm.typecpt = this.value
      axios.post(`http://localhost:3000/cptclient/`, this.cptForm)
      this.$router.push({
        name: 'ClientList'
      })
    }
  }
}
</script>

<style lang="css" scoped>
.align{
  text-align: right;
}
</style>



