<template>
  <el-dialog title="propriété utilisateur" :visible.sync="dialogFormVisible">
  <el-form :model="user">
    <el-form-item label="Nom" :label-width="formLabelWidth">
      <el-input v-model="user.name" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="e-Mail" :label-width="formLabelWidth">
  <el-input v-model="user.email" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="Administrateur" :label-width="formLabelWidth">
    <el-switch v-model="user.is_admin"></el-switch>
    </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="cancel">Annuler</el-button>
    <el-button type="primary" @click="onSubmit">Confirmer</el-button>
  </span>
</el-dialog>
</template>
<script>

import axios from 'axios'

export default {
  name: 'EditUsers',
  data () {
    return {
      formLabelWidth: '120px',
      dialogFormVisible: true,
      user: {},
      emailStorage: localStorage.getItem('email') || ''
    }
  },
  created () {
    axios.get(`http://localhost:3000/user/` + this.$route.params.id)
      .then(response => {
        this.user = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    cancel: function () {
      this.$router.push({
        name: 'UserList'
      })
    },
    onSubmit (evt) {
      evt.preventDefault()
      if (this.emailStorage === this.user.email) { return alert('utilisateur connecté, impossible de le modifier!') }
      axios.put(`http://localhost:3000/user/` + this.$route.params.id, this.user)
        .then(response => {
          this.$router.push({
            name: 'UserList'
          })
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>



