<template>
  <div id="ClientList">
    <Menu/>
    <div class="sizeTitle1">
      <span>Liste clients</span>
      </div>
      <br>
        <br>
        <div class="buttonNouveauClient">
          <el-button type="primary" @click="onNewClient">Nouveau client</el-button>
        </div>
        <br>
        <br>
        <el-table
          :data="clients"
          height="230"
          style="width: 100%"
          border>
          <el-table-column
            label="Nom"
            width="100"
            prop='nom'
            sortable>
          </el-table-column>
          <el-table-column
            label="Prénom"
            prop='prenom'
            width="120"
            sortable>
          </el-table-column>
          <el-table-column
            label="Adresse"
            prop='adresse'
            width="300">
          </el-table-column>
          <el-table-column
            v-if= "is_admin === 'true'"
            label="Opérations"
            width="250">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleDetail(scope.$index, scope.row)">
                Détail
              </el-button>
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">
                Editer
              </el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="deleteClient(scope.$index, scope.row)" circle></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
<script>
import axios from 'axios'
import Menu from './Menu'
export default {
  data () {
    return {
      visibleToken: localStorage.getItem('token'),
      active: true,
      clients: [],
      errors: [],
      pathos: [],
      name: localStorage.getItem('name') || '',
      is_admin: localStorage.getItem('is_admin') || ''
    }
  },
  components: {
    Menu
  },
  created () {
    console.log('tok', this.visibleToken)
    axios.get(`http://localhost:3000/client`)
      .then(response => {
        this.clients = response.data
        console.log('client', this.clients)
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    onNewClient () {
      this.$router.push('add-client')
    },
    alertDisplay () {
      // $swal function calls SweetAlert into the application with the specified configuration.
      this.$swal('Heading', 'this is a Heading', 'OK')
    },
    handleDetail (index, row) {
      this.$router.push({
        name: 'Compte',
        params: { id: row._id }
      })
    },
    handleEdit (index, row) {
      this.$router.push({
        name: 'EditClient',
        params: { id: row._id }
      })
    },
    logout: function () {
      this.$store.dispatch('logout')
        .then(() => {
          this.$router.push('/')
        })
    },
    deleteClient (index, row, rows) {
      var myThis = this
      myThis.$swal.fire({
        title: 'Etes vous sûr ?',
        text: 'Vous ne pourrez plus accéder',
        icon: 'warning',
        showCancelButton: true,
        showLoaderOnConfirm: true,
        confirmButtonText: 'Oui, supprimer !',
        cancelButtonText: 'Non, annuler !',
        cancelButtonColor: '#d33',
        confirmButtonColor: '#3085d6',
        preConfirm: function () {
          return new Promise(function (resolve, reject) {
            axios.delete('http://localhost:3000/client/' + row._id)
            setTimeout(function () {
              resolve()
            }, 4000)
            myThis.clients.splice(index, 1)
            myThis.$swal.fire(
              'Suppression effectuée !',
              'L\'utilisateur a été supprimé définitivement.',
              'success')
          })
        }
      })
    }
  }
}
</script>
<style>
.sizeTitle1{
  font-size: 20px;
  text-align: center;
  margin-top: 20px;
}
.buttonNouveauClient{
  margin-left: 40px;
}
.md-dialog-container{
  margin-top: 400px;
  border-radius: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 20px;
  margin-left: 50%;
  min-width: 700px;
  height: 600px;
}
</style>



