<template>
  <div>
        <div class="text"  v-show="visibleDeb=true">Choisir d'abord la nature du débit ou crédit</div>
    <md-field>
      <label>Débit</label>
      <span class="md-prefix">€</span>
      <md-input v-model="detailcpt.debit" v-show="value1!=''" @select="handclickDebit"
        type="string"></md-input>
    </md-field>
    <md-field>
      <label>Crédit</label>
      <span class="md-prefix">€</span>
      <md-input v-model="detailcpt.credit"  v-show="value2!=''"
        type="string"></md-input>
    </md-field>
    <div class="md-layout md-gutter">
      <div class="md-layout-item">
        <span>Nature débit et crédit</span>
        <md-field>
          <el-select v-model="value1" placeholder="Nature débit">
            <el-option
            v-for="item in options1"
            :key="item.type"
            :value="item.type">
          </el-option>
      </el-select>
      <el-select v-model="value2" placeholder="Nature crédit">
        <el-option
  v-for="item in options2"
  :key="item.type"
  :value="item.type">
</el-option>
  </el-select>
        </md-field>
      </div>
    </div>
      <div class="align">
    <md-button class="md-raised md-primary" @click="onSubmit">Ok</md-button>
    <md-button class="md-raised md-accent" @click="onCancel">Annulé</md-button>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data: () => ({
    visibleDeb: false,
    active: true,
    client: {},
    detailcpt: {
      debit: '',
      credit: ''
    },
    value1: '',
    value2: '',
    options1: [],
    options2: []
  }),
  created () {
    console.log('idc', this.$route.params.idcpt)
    axios.get(`http://localhost:3000/natdep/`)
      .then(response => {
        this.options1 = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
    axios.get(`http://localhost:3000/natcre/`)
      .then(response => {
        this.options2 = response.data
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  computed: {
    total: {
      get: function () {
        if (this.detailcpt.debit === '' && this.detailcpt.credit === '') { return Number(this.client.solde) }
        if (this.detailcpt.debit !== '') { return Number(this.client.solde) - Number(this.detailcpt.debit) }
        return Number(this.client.solde) + Number(this.detailcpt.credit)
      },
      set: function (newSolde) {
        this.solde = newSolde
      }
    }
  },
  methods: {
    handclickDebit () {
      this.visibleDeb = true
    },
    onSubmit (evt) {
      evt.preventDefault()
      this.detailcpt.user_id = this.$route.params.id
      this.detailcpt.numcpt = this.$route.params.numcpt
      if (this.value1 !== '') {
        this.detailcpt.nature = this.value1
        axios.post(`http://localhost:3000/detailcpt/`, this.detailcpt)
          .then(response => {
          })
          .catch(e => {
            this.errors.push(e)
          })
      } else {
        if (this.value2 !== '') this.detailcpt.nature = this.value2
        axios.post(`http://localhost:3000/detailcptcredit/`, this.detailcpt)
          .then(response => {
          })
          .catch(e => {
            this.errors.push(e)
          })
      }
      this.active = false
      this.$router.push({
        name: 'DetailsCpt',
        params: { idcpt: this.$route.params.idcpt, id: this.$route.params.id, numcpt: this.$route.params.numcpt, soldeplus: this.detailcpt.credit, soldemoins: this.detailcpt.debit }
      })
    },
    onCancel () {
      this.active = false
      this.$router.push({
        name: 'DetailsCpt',
        params: { id: this.$route.params.id, idcpt: this.$route.params.idcpt }
      })
    }
  }
}
</script>
<style media="screen">
.text{
  color: red;
}
.md-button{
  width: 50px;
}
.md-dialog{
  margin-top: 100px;
}
  .md-dialog-container{
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    margin-left: 50%;
    min-width: 600px;
    height: 500px;
  }
</style>



