<template>
  <div class="chgt" v-if="active">
    <el-dialog title="changement de mot de passe" :visible.sync="pwdFormVisible" width="60%">
      <el-form :model="formPwd">
        <el-form-item label="password" :label-width="formLabelWidth">
          <div class="input">
            <el-input type ='password' v-model="formPwd.password" placeholder="Veuillez saisir votre mot de passe" autocomplete="off"></el-input>
        </div>
        </el-form-item>
        <el-form-item label="Nouveau Password" :label-width="formLabelWidth">
          <el-input  type ='password' v-model="formPwd.newPassword" placeholder="Saisir le nouveau mot de passe">
          </el-input>
        </el-form-item>
        <el-form-item label="confirmer Password" :label-width="formLabelWidth">
          <el-input type ='password' v-model="formPwd.confNewPassword" placeholder="confirmer le nouveau mot de passe">
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="pwdFormVisible = false">Annuler</el-button>
        <el-button type="submit" variant="primary" @click="onValid">Valider</el-button>
      </span>
    </el-dialog>
      <md-dialog-title style="text-align=center">connexion à l'application</md-dialog-title>
    <md-field>
      <label>e-Mail</label>
      <md-input v-model="form.email"
       type="string"
         maxlength="30"
       ></md-input>
    </md-field>
    <md-field>
      <label>Password</label>
      <md-input v-model="form.password" type="password"></md-input>
    </md-field>
    <div class="align">
      <md-button class="md-raised md-primary" @click="onConnect">Ok</md-button>
      <md-button class="md-raised md-accent" @click="onCancel">Annulé</md-button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'StartLogin',
  data () {
    return {
      active: true,
      pwdFormVisible: false,
      id_User: '',
      formPwd: {
        password: '',
        newPassword: '',
        confNewPassword: ''
      },
      user: {
        password: ''
      },
      form: {
        name: '',
        is_admin: '',
        email: '',
        password: '',
        count: 0
      },
      id: '',
      formLabelWidth: '120px'
    }
  },
  methods: {
    onConnect (evt) {
      axios.post(`http://localhost:3000/user/authenticate`, this.form)
        .then(response => {
          console.log('buzz', response.data)
          // this.id = response.data.data.user._id
          if (response.data.status === 'error') {
            this.$message({
              message: 'Verifier votre identifiant ou mot de passe',
              type: 'warning'
            })
          } else {
            if (response.data.status === 'success') {
              this.dialogFormVisible = false
              if (response.data.data.user.count === 0) { this.pwdFormVisible = true }
              const token = response.data.data.token
              const is_admin = response.data.data.user.is_admin
              localStorage.setItem('token', token)
              localStorage.setItem('is_admin', is_admin)
              this.$router.push({
                name: 'ClientList'
              })
              this.active = false
            }
          }
        })
        .catch(errors => {
          if (errors === 'error') {
            alert('pas bon')
          }
        })
    },
    onCancel () {
      this.$message({
        message: 'Saisir vos credentiels.',
        type: 'warning'
      })
    },
    onValid (evt) {
      if (this.formPwd.password !== this.form.password) { alert('mauvais password') } else {
        if (this.formPwd.newPassword === this.form.password) { alert('password déja utilisé') } else {
          if (this.formPwd.newPassword !== this.formPwd.confNewPassword) { alert('verifier la confirmation du mot de passe') }
        }
      }
      this.user.password = this.formPwd.newPassword
      for (var i = 1; i <= 1; i++) {
        this.form.count = this.form.count + 1
      }
      this.user.count = this.form.count
      axios.put(`http://localhost:3000/user/` + this.id, this.user)
        .then(response => {
          this.pwdFormVisible = false
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>
<style>
.el-dialog{
  border-radius: 10px;
  font-weight: bold;
  color: blue;
}
.el-dialog__title{
  font-size: 25px
}
.el-input__inner{
  font-size: 20px;
}
.md-button{
  width: 50px;
}
.md-dialog{
  margin-top: 100px;
}
.md-dialog-title{
  text-align: center;
}
  .md-dialog-container{
    border-radius: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    margin-left: 50%;
    min-width: 400px;
    height: 300px;
  }
</style>



